import React, {Component} from "react";

import {BASE_URL} from "./../../../config/settings";
import Input from "./../../../components/Input";
import SelectPicker from "./../../../components/SelectPicker";

class CreateProduct extends Component {

    constructor(props) {
        super(props);
        this.state = {
            productName: "",
            price: "",
            brand: "",
            category: "",
            subCategory: "",
            subSubCategory: "",
            discount: "",
            productDesc: "",
            sizeAvailable: "",
            productSummary: "",
            coverImages: [],
            thumbnail: "",
            categoryList: [],
            thumbnailImage: "",
            coverImagesBase64: [],
            isUpdate: false
        }
    }

    componentDidMount() {
        fetch(`${BASE_URL}/categories/getCategories`, {
          method: "GET",
          headers: {
            "x-auth": localStorage["x-auth"]
          }
        }).then(response => {
            return response.json();
        }).then(res => {
            if(res) {
                this.setState({
                  categoryList: res
                })
            }
        }).catch(error => {
            console.log(error);
        });

        if(this.props && this.props.match && this.props.match.params && this.props.match.params.id) {
            this._fetchProductDetails(this.props.match.params.id);
        }
    }

    _fetchProductDetails = (productId) => {
        fetch(`${BASE_URL}/products/getProductDetails/${productId}`, {
          method: "GET",
          headers: {
            "x-auth": localStorage["x-auth"]
          }
        }).then(response => {
            return response.json();
        }).then(res => {
            if(res) {
                let thumbnailUrl = res.thumbnail.split("/");
                thumbnailUrl.shift();
                thumbnailUrl = thumbnailUrl.join("/");

                this.setState({
                  productName: res.productName,
                  price: res.price,
                  category: res.category,
                  subCategory: res.subCategory,
                  subSubCategory: res.subSubCategory,
                  discount: res.discount,
                  productDesc: res.productDesc,
                  productSummary: res.productSummary,
                  isUpdate: true,
                  brand: res.brand,
                  sizeAvailable: res.sizeAvailable.join(",")
                })
            }
        }).catch(error => {
            console.log(error);
        });
    }

    onChange = (key, value) => {
        this.setState({
            [key]: value
        });

        if (key === "thumbnail") {
            console.log(value);
            if (value) {
              this.getBase64(value, (result) => {
                  this.setState({
                      thumbnailImage: result
                  });
              });
            } else {
                this.setState({
                    thumbnailImage: ""
                });
            }
        }

        if (key === "coverImages") {
            this.setState({
                coverImagesBase64: []
            });
            let count = 0;
            for(let key in value) {
                  if (Object.keys(value).length === 0) {
                      this.setState({
                          coverImagesBase64: []
                      });
                  } else if (Object.keys(value).length === count) {
                      return false;
                  } else {
                      this.getBase64(value[count], (result) => {
                          this.setState({
                              coverImagesBase64: [
                                  ...this.state.coverImagesBase64,
                                  result
                              ]
                          });
                      });
                      count++;
                  }
            }
        }
    }

    getBase64 = (file, cb) => {
       var reader = new FileReader();
       reader.readAsDataURL(file);
       reader.onload = () => {
           cb(reader.result);
       };
       reader.onerror = (error) => {
         console.log('Error: ', error);
       }
   }

   onSaveProduct = () => {
      const {productName, price, brand, category, subCategory, subSubCategory, discount, productDesc, productSummary, coverImages, thumbnail} = this.state;

      const sizeAvailable = this.state.sizeAvailable.split(",");

      const productObject = {
          productName,
          price,
          category,
          subCategory,
          subSubCategory,
          discount,
          productDesc,
          sizeAvailable,
          productSummary,
          coverImages,
          thumbnail,
          brand
      };

      const formDataThumbnail = new FormData();
      formDataThumbnail.append('thumbnail', thumbnail);
      fetch(`${BASE_URL}/products/uploadProductThumbnail`, {
          method: "POST",
          body: formDataThumbnail,
          headers: {
            "x-auth": localStorage["x-auth"]
          }
      }).then(thumbnailResponse => {
          return thumbnailResponse.json();
      }).then(thumb => {
          productObject.thumbnail = thumb.path;
          const formData = new FormData();
          for(let i = 0; i < coverImages.length ; i++) {
              formData.append('coverImages', coverImages[i]);
          }
          return fetch(`${BASE_URL}/products/uploadCoverImages`, {
              method: "POST",
              body: formData,
              headers: {
                "x-auth": localStorage["x-auth"]
              }
          });
      }).then(coverImageRes => {
          return coverImageRes.json();
      }).then(coverImage => {
          productObject.coverImages = []
          coverImage.forEach(image => {
              productObject.coverImages.push(image.path)
          });
          return fetch(`${BASE_URL}/products/createProduct`, {
              method: "POST",
              body: JSON.stringify(productObject),
              headers: {
                  "Accept": "application/json",
                  "Content-Type": "application/json",
                  "x-auth": localStorage["x-auth"]
              }
          });
      }).then(productResponse => {
          return productResponse.json();
      }).then(productRes => {
          alert("Product Saved");
          this.props.history.push("/products");
      }).catch(error => {
          console.log(error);
      })
   }

   onUpdateProduct = () => {
      const {productName, brand, price, category, subCategory, subSubCategory, discount, productDesc, productSummary} = this.state;
      const sizeAvailable = this.state.sizeAvailable.split(",");
      const productObject = {
          productName,
          price,
          category,
          subCategory,
          subSubCategory,
          discount,
          productDesc,
          sizeAvailable,
          productSummary,
          brand
      };

      fetch(`${BASE_URL}/products/updateProduct/${this.props.match.params.id}`, {
          method: "PUT",
          body: JSON.stringify(productObject),
          headers: {
              "Accept": "application/json",
              "Content-Type": "application/json",
              "x-auth": localStorage["x-auth"]
          }
      }).then((res) => {
          alert("Product Updated");
          this.props.history.push("/products");
      }).catch(error => {
          console.log(error);
      });

   }

    render() {
        const getFilteredCategoryList = this.state.categoryList.filter((category) => {
            return category.categoryAlias === this.state.category;
        });

        let renderSubCategoryList = [];
        let renderSubSubCategoryList = [];

        if (getFilteredCategoryList.length > 0 && getFilteredCategoryList[0].subCategories && getFilteredCategoryList[0].subCategories.length > 0) {
            renderSubCategoryList = (
               <SelectPicker
                   defaultValue="Select Sub Category"
                   onChange={event => this.onChange("subCategory", event.target.value)}
                   value={this.state.subCategory}>
                   {getFilteredCategoryList[0].subCategories.map((subCategory, index) => {
                      return (
                          <option value={subCategory.subCategoryAlias} key={index}>{subCategory.subCategoryName}</option>
                      )
                    })
                    }
                </SelectPicker>
            )

            const getFilteredSubCategoryList = getFilteredCategoryList[0].subCategories.filter((subCategory) => {
                return subCategory.subCategoryAlias === this.state.subCategory;
            });

            if (getFilteredSubCategoryList.length > 0 && getFilteredSubCategoryList[0].subCategoriesTwo && getFilteredSubCategoryList[0].subCategoriesTwo.length > 0) {
                renderSubSubCategoryList = (
                  <SelectPicker
                      defaultValue="Select Sub Sub Category"
                      onChange={event => this.onChange("subSubCategory", event.target.value)}
                      value={this.state.subSubCategory}>
                      {getFilteredSubCategoryList[0].subCategoriesTwo.map((subSubCategory, index) => {
                         return (
                             <option value={subSubCategory.subCategoryTwoAlias} key={index}>{subSubCategory.subCategoryTwoName}</option>
                         )
                       })
                       }
                  </SelectPicker>
               )
            }
        }

        return (
            <div>
                <div className="card">
                    <div className="card-header">
                        <strong>Products</strong>
                    </div>
                    <div className="card-body">
                        <div className="row">
                            <div className="col-md-6">
                                <SelectPicker
                                    onChange={event => this.onChange("category", event.target.value)}
                                    value={this.state.category}>
                                    {this.state.categoryList.map((category, index) => {
                                        return (
                                            <option value={category.categoryAlias} key={index}>{category.categoryName}</option>
                                        )
                                    })}
                                </SelectPicker>
                                {renderSubCategoryList}
                                {renderSubSubCategoryList}
                                <Input
                                    placeholder= "Product Name"
                                    value={this.state.productName}
                                    onChange={event => this.onChange("productName", event.target.value)}/>
                                <Input
                                    placeholder= "Product Brand"
                                    value={this.state.brand}
                                    onChange={event => this.onChange("brand", event.target.value)}/>
                                <Input
                                    placeholder= "Product Price"
                                    value={this.state.price}
                                    onChange={event => this.onChange("price", event.target.value)}/>
                                <Input
                                    placeholder= "Discount"
                                    value={this.state.discount}
                                    onChange={event => this.onChange("discount", event.target.value)}/>
                                <Input
                                    placeholder= "Product Description"
                                    value={this.state.productDesc}
                                    onChange={event => this.onChange("productDesc", event.target.value)}/>
                                <Input
                                    placeholder= "Product Summary"
                                    value={this.state.productSummary}
                                    onChange={event => this.onChange("productSummary", event.target.value)}/>
                                <Input
                                    placeholder= "Size Available"
                                    value={this.state.sizeAvailable}
                                    onChange={event => this.onChange("sizeAvailable", event.target.value)}/>
                            </div>
                            {!this.state.isUpdate &&
                                <div className="col-md-6">
                                    <Input
                                        type="file"
                                        label="Choose Thumbnail"
                                        columnLabel="col-md-12"
                                        onChange={event => this.onChange("thumbnail", event.target.files[0])}/>
                                    {this.state.thumbnailImage !== "" &&
                                      <div className="clearfix">
                                        <img src={this.state.thumbnailImage} className="img-thumbnail rounded float-left thumbnail-image"/>
                                      </div>
                                    }
                                    <Input
                                        type="file"
                                        label="Choose Cover (You Can Select Multiple Images)"
                                        columnLabel="col-md-12"
                                        multiple={true}
                                        onChange={event => this.onChange("coverImages", event.target.files)}/>
                                    {this.state.coverImagesBase64.length > 0 && this.state.coverImagesBase64.map((image, index) => {
                                        return (
                                          <img key={index} src={image} className="img-thumbnail rounded float-left thumbnail-image"/>
                                        )
                                      })
                                    }
                                </div>
                            }
                            <div className="col-6 col-sm-4 col-md-2 col-xl mb-3 mb-xl-0">
                              {this.state.isUpdate ?
                                <button className="btn btn-primary float-xl-right" type="button" onClick={this.onUpdateProduct}>
                                    Update Product
                                </button>
                              :
                                <button className="btn btn-primary float-xl-right" type="button" onClick={this.onSaveProduct}>
                                    Save Product
                                </button>
                              }
                            </div>

                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default CreateProduct;



