import React, {Component} from "react";
import _ from "lodash";

import {BASE_URL} from "./../../../config/settings";
import Input from "./../../../components/Input";
import SelectPicker from "./../../../components/SelectPicker";

class Categories extends Component {

    constructor(props) {
        super(props);
        this.state = {
            isCreateCategory: false,
            categoryName: "",
            iconName: "",
            subCategoryName: "",
            categoryList: [],
            selectedCategory: "",
            selectedCategoryTwo: "",
            selectedSubCategory: "",
            subCategoryTwoName: ""
        }
    }

    componentDidMount() {
        fetch(`${BASE_URL}/categories/getCategories`, {
          method: "GET",
          headers: {
            "x-auth": localStorage["x-auth"]
          }
        }).then(response => {
            return response.json();
        }).then(res => {
            if(res) {
                this.setState({
                  categoryList: res
                })
            }
        }).catch(error => {
            console.log(error);
        });
    }

    onChange = (key, value) => {
        this.setState({
            [key]: value
        })
    }

    onClickCreateCategory = () => {
        this.setState({
            isCreateCategory: true
        })
    }

    onAddCategory = () => {
        if(this.state.categoryName !== "" && this.state.iconName !== "") {
            const category = {
                categoryName: this.state.categoryName,
                iconName: this.state.iconName,
                categoryAlias: `${this.state.categoryName.toLowerCase().replace(/\s/g,'')}`
            }
            this.setState({
                categoryList:[
                    ...this.state.categoryList,
                    category
                ],
                categoryName: "",
                iconName: ""
            });
        }
    }

    onAddSubCategory = () => {
        if (this.state.selectedCategory === "" || this.state.subCategoryName === "") {
            return false;
        }

        const subCategory = {
            subCategoryName: this.state.subCategoryName,
            subCategoryAlias: `${this.state.selectedCategory.toLowerCase().replace(/\s/g,'')}-${this.state.subCategoryName.toLowerCase().replace(/\s/g,'')}`
        }

        const categoryList = JSON.parse(JSON.stringify(this.state.categoryList));

        categoryList.forEach(category => {
            if(category.categoryName === this.state.selectedCategory) {
                if (category.hasOwnProperty("subCategories") && category.subCategories.length > 0) {
                    category.subCategories.push(subCategory)
                } else {
                  category.subCategories = [subCategory]
                }
            }
        });

        this.setState({
            categoryList,
            subCategoryName: ""
        });
    }

    onAddSubSubCategory = () => {
        if (this.state.selectedCategoryTwo === "" || this.state.selectedSubCategory === "" || this.state.subCategoryTwoName === "") {
            return false;
        }

        const subSubCategory = {
            subCategoryTwoName: this.state.subCategoryTwoName,
            subCategoryTwoAlias: `${this.state.selectedCategoryTwo.toLowerCase().replace(/\s/g,'')}-${this.state.selectedSubCategory.toLowerCase().replace(/\s/g,'')}-${this.state.subCategoryTwoName.toLowerCase().replace(/\s/g,'')}`
        }

        const categoryList = JSON.parse(JSON.stringify(this.state.categoryList));

        categoryList.forEach(category => {
            if(category.categoryName === this.state.selectedCategoryTwo && category.subCategories.length > 0) {
                category.subCategories.forEach(subCategory => {
                    if (subCategory.subCategoryName === this.state.selectedSubCategory) {
                        if (subCategory.hasOwnProperty("subCategoriesTwo") && subCategory.subCategoriesTwo.length > 0) {
                            subCategory.subCategoriesTwo.push(subSubCategory)
                        } else {
                          subCategory.subCategoriesTwo = [subSubCategory]
                        }
                    }
                });
            }
        });

        this.setState({
            categoryList,
            subCategoryTwoName: ""
        });
    }

    onDeleteCategory = (cat) => {
        let categoryList = JSON.parse(JSON.stringify(this.state.categoryList));
        categoryList = categoryList.filter(category => (category.categoryName !== cat.categoryName));
        this.setState({
            categoryList
        });
    }

    onDeleteSubCategory = (cat, subCat) => {
        const categoryList = JSON.parse(JSON.stringify(this.state.categoryList));
        categoryList.forEach(category => {
            if(category.categoryName === cat.categoryName) {
                category.subCategories = category.subCategories.filter(subCategory => (subCategory.subCategoryName !== subCat.subCategoryName))
            }
        });
        this.setState({
            categoryList
        });
    }

    onDeleteSubSubCategory = (cat, subCat, subSubCat) => {
        const categoryList = JSON.parse(JSON.stringify(this.state.categoryList));
        categoryList.forEach(category => {
            if(category.categoryName === cat.categoryName) {
                category.subCategories.forEach(subCategory => {
                    if (subCategory.subCategoryName === subCat.subCategoryName) {
                        subCategory.subCategoriesTwo = subCategory.subCategoriesTwo.filter(subSubCategory => (subSubCategory.subCategoryTwoName !== subSubCat.subCategoryTwoName))
                    }
                })
            }
        });
        this.setState({
            categoryList
        });
    }

    onSaveCategories = () => {
        let count = 0;
        fetch("http://localhost:4000/categories/deleteAllCategories", {
            method: "DELETE",
            headers: {
              "x-auth": localStorage["x-auth"]
            }
        }).then((res) => {
            this.state.categoryList.forEach(category => {
                fetch("http://localhost:4000/categories/createCategory", {
                    headers: {
                        "Accept": "application/json",
                        "Content-Type": "application/json",
                        "x-auth": localStorage["x-auth"]
                    },
                    method: "POST",
                    body: JSON.stringify(category)
                }).then(response => {
                    count++;
                    if (this.state.categoryList.length === count) {
                        alert("Categories Saved")
                    }
                    console.log(response);

                }).catch(error => {
                    console.log(error);
                })
            });
        }).catch(error => {
            console.log(error);
        })
    }

    render() {
        const getSubCategoryList = this.state.categoryList.filter((category) => {
            return category.categoryName === this.state.selectedCategoryTwo;
        });

        let renderSubCategoryList = [];

        if (getSubCategoryList.length > 0 && getSubCategoryList[0].subCategories) {
           renderSubCategoryList = getSubCategoryList[0].subCategories.map((subCategory, index) => {
                return (
                    <option value={subCategory.subCategoryName} key={index}>{subCategory.subCategoryName}</option>
                )
            });
        }

        return (
          <div>
              <div className="card">
                  <div className="card-header">
                      <strong>Categories</strong>
                  </div>
                  <div className="card-body">
                    {(!this.state.isCreateCategory && this.state.categoryList.length < 1)  ?
                      <div>
                          <div className="alert alert-secondary" role="alert">You have no categories at this moment.</div>
                          <div className="row align-items-center">
                              <div className="col-6 col-sm-4 col-md-2 col-xl mb-3 mb-xl-0">
                                  <button className="btn btn-primary" type="button" onClick={this.onClickCreateCategory}>
                                      Create Category
                                  </button>
                              </div>
                          </div>
                      </div>
                      :
                      <div className="row">
                          <div className="col-md-6">
                              <Input
                                  placeholder= "Category Name"
                                  value={this.state.categoryName}
                                  onChange={event => this.onChange("categoryName", event.target.value)}/>
                              <Input
                                  placeholder= "Icon Name"
                                  value={this.state.iconName}
                                  onChange={event => this.onChange("iconName", event.target.value)}/>
                              <div className="row align-items-center">
                                  <div className="col-6 col-sm-4 col-md-2 col-xl mb-3 mb-xl-0">
                                      <button className="btn btn-primary" type="button" onClick={this.onAddCategory}>
                                          Add Category
                                      </button>
                                  </div>
                              </div>
                              <br/>
                              {this.state.categoryList.length > 0 &&
                                <div>
                                  <p>Add Sub Category</p>
                                  <hr />
                                  <SelectPicker onChange={event => this.onChange("selectedCategory", event.target.value)} value={this.state.selectedCategory}>
                                      {this.state.categoryList.map((category, index) => {
                                          return (
                                              <option value={category.categoryName} key={index}>{category.categoryName}</option>
                                          )
                                      })}
                                  </SelectPicker>
                                  <Input
                                      placeholder= "Sub Category Name"
                                      value={this.state.subCategoryName}
                                      onChange={event => this.onChange("subCategoryName", event.target.value)}/>

                                  <div className="row align-items-center">
                                      <div className="col-6 col-sm-4 col-md-2 col-xl mb-3 mb-xl-0">
                                          <button className="btn btn-primary" type="button" onClick={this.onAddSubCategory}>
                                              Add Sub Category
                                          </button>
                                      </div>
                                  </div>

                                </div>
                              }
                              <br/>
                              {this.state.categoryList.length > 0 &&
                                <div>
                                  <p>Add Sub Sub Category</p>
                                  <hr />
                                  <SelectPicker onChange={event => this.onChange("selectedCategoryTwo", event.target.value)} value={this.state.selectedCategoryTwo}>
                                      {this.state.categoryList.map((category, index) => {
                                          return (
                                              <option value={category.categoryName} key={index}>{category.categoryName}</option>
                                          )
                                      })}
                                  </SelectPicker>
                                  {this.state.selectedCategoryTwo &&
                                      <div>
                                          <SelectPicker
                                              defaultValue="Select Sub Category"
                                              onChange={event => this.onChange("selectedSubCategory", event.target.value)}
                                              value={this.state.selectedSubCategory}>
                                              {renderSubCategoryList}
                                          </SelectPicker>
                                          <Input
                                              placeholder= "Sub Category Name"
                                              value={this.state.subCategoryTwoName}
                                              onChange={event => this.onChange("subCategoryTwoName", event.target.value)}/>
                                          <div className="row align-items-center">
                                              <div className="col-6 col-sm-4 col-md-2 col-xl mb-3 mb-xl-0">
                                                  <button className="btn btn-primary" type="button" onClick={this.onAddSubSubCategory}>
                                                      Add Sub Category
                                                  </button>
                                              </div>
                                          </div>
                                      </div>
                                  }
                                </div>
                              }
                          </div>
                          <div className="col-md-6">
                              <ul>
                              {this.state.categoryList.length > 0 && this.state.categoryList.map((category, index) => {
                                  return (
                                    <li key={index}>
                                      <div>
                                          <div className="list-item">{category.categoryName} <span className="delete-icon" onClick={() => this.onDeleteCategory(category)}>X</span></div>
                                          {(category.subCategories && category.subCategories.length > 0) &&
                                              <ul>
                                                {category.subCategories.map((subCategory, index) => {
                                                    return (
                                                      <li key={index}>
                                                        <div>
                                                          <div className="list-item">{subCategory.subCategoryName} <span className="delete-icon" onClick={() => this.onDeleteSubCategory(category, subCategory)}>X</span></div>
                                                          {(subCategory.subCategoriesTwo && subCategory.subCategoriesTwo.length > 0) &&
                                                              <ul>
                                                                {subCategory.subCategoriesTwo.map((subSubCategory, index) => {
                                                                    return (
                                                                      <li key={index}>
                                                                        <div className="list-item">{subSubCategory.subCategoryTwoName} <span className="delete-icon" onClick={() => this.onDeleteSubSubCategory(category, subCategory, subSubCategory)}>X</span></div>
                                                                      </li>
                                                                    )
                                                                 })
                                                                }
                                                              </ul>
                                                          }
                                                        </div>
                                                      </li>
                                                    )
                                                }) }
                                              </ul>
                                          }
                                      </div>
                                    </li>
                                  )
                              })}
                              </ul>
                              {this.state.categoryList.length > 0 &&
                                <div className="row align-items-center">
                                    <div className="col-6 col-sm-4 col-md-2 col-xl mb-3 mb-xl-0">
                                        <button className="btn btn-primary float-xl-right" type="button" onClick={this.onSaveCategories}>
                                            Save Category
                                        </button>
                                    </div>
                                </div>
                              }
                          </div>
                      </div>
                    }
                  </div>
              </div>
          </div>
        );
    }
}

export default Categories;



