import React, {Component} from "react";

import {BASE_URL} from "./../../config/settings";

class Login extends Component {

    constructor(props) {
        super(props);
        this.state = {
            email: "",
            password: ""
        }
    }

    componentDidMount() {}

    handleChange = (key, e) => {
        this.setState({
            [key]: e.target.value
        });
    }

    requestLogin = (e) => {

        e.preventDefault();

        const reqBody = {
            email: this.state.email,
            password: this.state.password
        }

        fetch(`${BASE_URL}/users/login`, {
            method: "POST",
            headers: {
              "Accept": "application/json",
              "Content-Type": "application/json"
            },
            body: JSON.stringify(reqBody)

        }).then(response => {
          
            if (response.status === 200 && response.headers.get("x-auth")) {
               const token = response.headers.get("x-auth");
               localStorage.setItem("x-auth", token);
               this.props.history.push("/");
               return response.json();
           } else if (response.status === 400) {
               throw new Error("Wrong credentials");
           } else {
               throw new Error(response);
           }

        }).then(user => {
          console.log(user);
        }).catch(error => {
            console.log(error);
        })
    }

    render() {
        return(
          <div className="app flex-row align-items-center">
            <div className="container">
              <div className="row justify-content-center">
                <div className="col-md-4">
                  <div className="card-group">
                    <div className="card p-4">
                      <div className="card-body">
                        <form onSubmit={this.requestLogin}>
                            <h1>Login</h1>
                            <p className="text-muted">Sign In to your account</p>
                            <div className="input-group mb-3">
                              <div className="input-group-prepend">
                                <span className="input-group-text">
                                  <i className="icon-user"></i>
                                </span>
                              </div>
                              <input className="form-control" type="text" placeholder="Email" value={this.state.email} onChange={(e) => this.handleChange("email", e)}/>
                            </div>
                            <div className="input-group mb-4">
                              <div className="input-group-prepend">
                                <span className="input-group-text">
                                  <i className="icon-lock"></i>
                                </span>
                              </div>
                              <input className="form-control" type="password" placeholder="Password" value={this.state.password} onChange={(e) => this.handleChange("password", e)} />
                            </div>
                            <div className="row">
                              <div className="col-6">
                                <button className="btn btn-primary px-4" type="submit">Login</button>
                              </div>
                            </div>
                          </form>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        )
    }
}


export default Login



