import React, {Component} from "react";
import {withRouter} from "react-router-dom";

import {BASE_URL} from "./../../config/settings";

class Breadcrumb extends Component {

    requestLogout = () => {
      fetch(`${BASE_URL}/users/logout`, {
          method: "DELETE",
          headers: {
              "x-auth": localStorage["x-auth"]
          }
      }).then(response => {
          if(response.status === 200) {
              localStorage.removeItem("x-auth");
              this.props.history.replace("/login")
          }
      }).catch(error => {
          console.log(error);
      })
    }

    render() {
        return (
          <ol className="breadcrumb">
            <li className="breadcrumb-item">Home</li>
            <li className="breadcrumb-item">
              <a href="#">Admin</a>
            </li>
            <li className="breadcrumb-item active">Dashboard</li>
            <li className="breadcrumb-menu d-md-down-none">
              <div className="btn-group" role="group" aria-label="Button group">
                <a className="btn" href="#">
                  <i className="icon-speech"></i>
                </a>
                <a className="btn" href="./">
                  <i className="icon-graph"></i> &nbsp;Dashboard</a>
                <a className="btn" href="#">
                  <i className="icon-settings"></i> &nbsp;Settings</a>
                <a className="btn" onClick={this.requestLogout}>
                  <i className="icon-settings"></i> &nbsp;Logout</a>
              </div>
            </li>
          </ol>
        );
    }
}

export default withRouter(Breadcrumb);



