import React, {Component} from "react";

import {BASE_URL} from "./../../../config/settings";
import Input from "./../../../components/Input";
import SelectPicker from "./../../../components/SelectPicker";

class Products extends Component {

    constructor(props) {
        super(props);
        this.state = {
            productList: []
        }
    }

    componentDidMount() {
        this._fetchProductList();
    }

    _fetchProductList = () => {
        fetch(`${BASE_URL}/products/getProducts`, {
          method: "GET",
          headers: {
            "x-auth": localStorage["x-auth"]
          }
        }).then(response => {
            return response.json();
        }).then(res => {
            if(res) {
                this.setState({
                  productList: res
                })
            }
        }).catch(error => {
            console.log(error);
        });
    }

   renderProductList = () => {
      return this.state.productList.map(product => {
          return (
              <tr key={product._id}>
                  <td>{product.productName}</td>
                  <td>{product.brand}</td>
                  <td>{product.category}</td>
                  <td>{product.price}</td>
                  <td>{product.discount}</td>
                  <td>{product.status ? <span className="badge badge-success">Active</span> : <span className="badge badge-secondary">Inactive</span>}</td>
                  <td>
                      <button className="btn btn-sm btn-primary" onClick={() => this.editProduct(product._id)}>Edit</button>&nbsp;
                      <button className="btn btn-sm btn-warning" onClick={() => this.changeProductStatus(product._id, product.status)}>{product.status ? "Deactivate" : "Activate"}</button>&nbsp;
                      <button className="btn btn-sm btn-danger" onClick={() => this.deleteProduct(product._id)}>Delete</button>
                  </td>
              </tr>
          )
      })
   }

   editProduct = (productId) => {
      this.props.history.push(`/addProduct/${productId}`)
   }

   changeProductStatus = (productId, status) => {
    const requestBody = {
        status: !status
    }
     fetch(`${BASE_URL}/products/updateProduct/${productId}`, {
       method: "PUT",
       body: JSON.stringify(requestBody),
       headers: {
           "Accept": "application/json",
           "Content-Type": "application/json",
           "x-auth": localStorage["x-auth"]
       }
     }).then(response => {
         return response.json();
     }).then(res => {
         this._fetchProductList();
     }).catch(error => {
         console.log(error);
     });
   }


    deleteProduct = (productId) => {
        fetch(`${BASE_URL}/products/deleteProduct/${productId}`, {
          method: "DELETE",
          headers: {
            "x-auth": localStorage["x-auth"]
          }
        }).then(response => {
            this._fetchProductList();
        }).catch(error => {
            console.log(error);
        });
    }

    render() {
        return (
            <div>
                <div className="card">
                    <div className="card-header">
                        <strong>Product List</strong>
                    </div>
                    <div className="card-body">
                        <div className="row align-items-center">
                            <div className="col-6 col-sm-4 col-md-2 col-xl mb-3 mb-xl-0">
                                <button className="btn btn-primary" type="button" onClick={() => this.props.history.push("/addProduct")}>
                                    Add Product
                                </button>
                            </div>
                        </div>
                        <br />
                        <table className="table table-responsive-sm">
                        <thead>
                            <tr>
                                <th>Product Name</th>
                                <th>Brand</th>
                                <th>Category</th>
                                <th>Price</th>
                                <th>discount</th>
                                <th>Status</th>
                                <th>Actions</th>
                            </tr>
                        </thead>
                          <tbody>
                              {this.renderProductList()}
                          </tbody>
                        </table>
                    </div>
                </div>
            </div>
        );
    }
}

export default Products;



