import React, {Component} from "react";

const defaultProps = {
    label: "",
    columnLabel: "col-md-3",
    columnInput: "col-md-9",
    onChange: () => {},
    defaultValue: "Select Category"

}
class SelectPicker extends Component {

    render() {
        return (
          <div className="form-group row">
              {this.props.label !== "" && <label className={`${this.props.columnLabel} col-form-label`} htmlFor="category">{this.props.label}</label>}
              <div className={this.props.columnInput}>
                  <select
                      className="form-control"
                      value={this.props.value}
                      onChange={this.props.onChange}>
                      <option>{this.props.defaultValue}</option>
                      {this.props.children}
                  </select>
              </div>
          </div>
        );
    }
}

SelectPicker.defaultProps = defaultProps;

export default SelectPicker;



