import React, {Component} from "react";
import {Link} from "react-router-dom";

class Sidebar extends Component {

    render() {
        return (
          <div className="sidebar">
            <nav className="sidebar-nav ps">
              <ul className="nav">
                <li className="nav-item open">
                  <Link to="/dashboard" className="nav-link active">
                    <i className="nav-icon icon-speedometer"></i> Dashboard
                  </Link>
                </li>
                <li className="nav-title">Theme</li>
                <li className="nav-item">
                  <Link className="nav-link" to="/categories">
                    <i className="nav-icon icon-drop"></i> Categories
                  </Link>
                </li>
                <li className="nav-item">
                  <Link className="nav-link" to="/products">
                    <i className="nav-icon icon-pencil"></i> Products
                  </Link>
                </li>
              </ul>
            </nav>
          </div>
        );
    }
}

export default Sidebar;



